<template>
  <div class="member-manage">
    <div class="member-add-form">
      <h4></h4>
      <c-input autoFocus clearAble :width="230" placeholder="" v-model="email"/>
      <c-button size="normal" type="primary" @click="onAdd"></c-button>
    </div>
    <c-data-grid :store="userList" show-index indexTitle="">
      <c-grid-column field="name" title="" sort-able></c-grid-column>
      <c-grid-column field="email" title="" sort-able></c-grid-column>
      <c-grid-column field="roleName" title="" sort-able></c-grid-column>
      <c-grid-column field="_" title="">
        <template slot-scope="{data,field}">
          <c-button type="danger" :disable="data.roleId === 0" @click="onDel(data.id)"></c-button>
        </template>
      </c-grid-column>
    </c-data-grid>
  </div>
</template>

<script>
import {actions} from '../../store/constants'
import {mapState} from 'vuex'
import FormItem from '../../components/formItem/index'
export default {
  name: 'memberManage',
  components: {FormItem},
  data () {
    return {
      email: ''
    }
  },
  computed: {
    ...mapState({
      currentProject (state) {
        return state.project.currentProject
      },
      userList (state) {
        return state.user.memberList
      }
    })
  },
  created () {
    this.$store.dispatch(actions.user.query, this.currentProject.id)
  },
  methods: {
    onDel (id) {
      this.$store.dispatch(actions.user.removeMember, {
        projectId: this.currentProject.id,
        userId: id
      }).then(() => {
        this.$message({
          message: ''
        })
      }).catch(err => {
        this.$message({
          message: '',
          type: 'error'
        })
        console.error('', err)
      })
    },
    onAdd () {
      if (!this.email) {
        this.$message({
          message: '',
          type: 'error'
        })
        return
      }
      this.$store.dispatch(actions.user.invite, {
        email: this.email,
        projectId: this.currentProject.id
      }).then(() => {
        this.$message({
          message: ''
        })
      }).catch(err => {
        this.$message({
          message: '',
          type: 'error'
        })
        console.error('', err)
      })
    }
  }
}
</script>

<style lang="sass">
@import "styles/member"
</style>
