<template>
  <div class="login">
    <span class="brand">CleverMock</span>
    <div class="form-panel">
      <div class="title"></div>
      <c-input size="big" v-model="username" autoFocus clear-able placeholder=""/>
      <c-input size="big" v-model="password" type="password" placeholder=""/>
      <c-button size="large" type="primary" @click="onLogin"></c-button>

      <div class="ref-links">
        <a href="javascript:void(0)" @click="toReg"></a><br/>
        <a href="http://www.miitbeian.gov.cn"
           target="_blank"
           style="display: block; text-align: center;margin-top: 20px">ICP18028016-1</a>
      </div>
    </div>
  </div>
</template>

<script>
import {actions} from '../../store/constants'
import {router} from '../../router/constants'
export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    onLogin () {
      this.$store.dispatch(actions.user.login, {
        username: this.username,
        password: this.password
      }).then(() => {
        this.$router.replace({
          name: router.project.manage
        })
      }).catch(err => {
        this.$message({
          type: 'error',
          message: ''
        })
        console.error('', err)
      })
    },
    toReg () {
      this.$router.push({
        name: router.register
      })
    }
  }
}
</script>

<style lang="sass">
@import "styles/index"
</style>
